.main_projetos {
  display: flex;
  flex-direction: column;
  gap: calc(85vh - 80vh);
  width: 100%;
}

.section_projetos {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  padding: 1rem;
}

@media screen and (max-width: 500px) {
  .nav_header ul {
    grid-template-columns: 1fr;
  }
}
