.section_projetos {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  gap: calc(3rem + 0.5vh);
  padding: 1rem;
}
.section_projetos div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
}

.section_projetos .infoProjeto {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: calc(2rem + 0.5vh);
  max-width: calc(100% - 5vw);
  margin: auto;
  letter-spacing: 0.2rem;
  padding: 1rem;
}
.imgProjeto {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  gap: 2rem;
}

.imgProjeto video {
  max-width: calc(100% - 20%);
  height: calc(100% - 20%);
}
.section_projetos div a {
  padding: 10px;
  border: 1px solid var(--color-green);
  transition: transform 0.2s;
}
.section_projetos div a:hover {
  border: 1px solid var(--color-white-green);
  color: var(--color-white-green);
  transform: scale(1.05);
}
.section_projetos .flexInline {
  flex-direction: row;
  align-items: center;
}
.section_projetos .centerLinks {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: calc(2rem + 3vh);
}

@media screen and (min-width: 768px) {
  .section_projetos .infoProjeto {
    max-width: calc(100% - 15vw);
  }
}
