.header {
    padding-top: 0.3rem;
    background-color: rgba(24, 24, 24, 0.8);
  }
  
  .hr_header {
    background-color: var(--color-green);
    border: none;
    height: 1px;
  }
  .nav_header {
    width: 100%;
    background-color: rgba(24, 24, 24, 0.8);
  }
  .nav_header ul {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    list-style: none;
  }
  .ul_projetos li {
    margin: auto;
    width: 100%;
  }
  
  .ul_projetos li a {
    text-align: center;
    margin: auto;
    display: block;
    width: 100%;
    height: 100%;
    padding: 2rem;
    text-decoration: none;
  }
  .select_a {
    background-color: rgba(27, 27, 27, 0.8);
    filter: brightness(120%);
  }
  .ul_projetos li a:hover {
    background-color: rgba(27, 27, 27, 0.8);
    text-decoration: underline;
    filter: brightness(120%);
    cursor: pointer;
  }
  
  .ul_projetos li a:visited {
    color: var(--color-p);
  }
  
  .ul_projetos li a:link {
    color: var(--color-p);
  }