.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  height: 80vh;
}

.titulo_icone {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: calc(8px + 1vh);
  list-style: none;
}

.section .box_border a {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-direction: row-reverse;
  color: var(--color-green);
}
.section .box_border {
  padding: 10px;
  border: 1px solid var(--color-green);
  min-width: calc(250px + 5vw);
  text-align: center;
  transition: transform 0.2s;
}
.section .box_border:hover {
  border: 1px solid var(--color-white-green);
  color: var(--color-white-green);
  transform: scale(1.05);
}
.logo {
  max-width: calc(20px + 1vh);
}
@media screen and (max-width: 1308px) {
  .section .box_border {
    min-width: 100%;
  }
}
